// Better display in metabox.
.postbox-container {
  .cmb2-metabox {
    overflow: hidden;
    margin: -6px -12px -12px -12px;
  }

  .cmb2-wrap > .cmb-field-list .cmb-row {
    &:not(.cmb-repeatable-grouping) {
      margin: 0;
      padding: 0.7em 1em;
    }

    .cmb-td > p.cmb2-metabox-description {
      color: #7b7b7b;
      font-style: normal;
      display: block;
      padding-top: 5px;
    }
  }

  .cmb2-wrap > .cmb-field-list .cmb-row.cmb-type-title {
    background-color: #fafafa;

    .cmb2-metabox-title {
      font-size: 12px;
      text-transform: uppercase;

      padding-top: 0;
      margin-bottom: 0;
    }

    .cmb2-metabox-description {
      padding-top: 2px;
    }
  }
}
